import './App.css';

import { Input, Button, Loading } from '@nextui-org/react';
import { FontAwesome } from './components/FontAwesome';
import { useState, useRef } from 'react';
import axios from 'axios';
import { toast } from 'react-toastify';

function App() {
	const [loading, setLoading] = useState(false);
	const [video, setVideo] = useState({});
	const [notFound, setNotFound] = useState(false);
	const [isDownloading, setDownloading] = useState(false);
	const input = useRef();

	const baseApiUrl = '/api';
	const loadVideo = async () => {
		setLoading(true);

		if (!input.current.value.length) return input.current.focus() + setLoading(false);

		const id =
			input.current.value.match(/^.*((youtu.be\/)|(v\/)|(\/u\/\w\/)|(embed\/)|(watch\?))\??v?=?([^#&?]*).*/)?.at(7) ??
			input.current.value;

		const response = await fetch(`${baseApiUrl}/getVideo?id=${id}`)
			.then(res => res.json())
			.catch(() => null);

		if (response && !response.error) setVideo(response);
		else setNotFound(true);
		setLoading(false);
	};

	const downloadVideo = async ([videoiTag, audioiTag]) => {
		setDownloading(true);
		const toasted = toast.loading('Convirtiendo video...', {
			position: 'bottom-center',
			theme: 'dark',
			closeOnClick: false,
			closeButton: false,
			autoClose: false
		});

		const data = await axios
			.post(`${baseApiUrl}/convert`, {
				url: video.videoDetails.videoId,
				'video-itag': videoiTag || 'none',
				'audio-itag': audioiTag,
				'convert-mp3': videoiTag ? undefined : true
			})
			.catch(() => null);

		if (data && !data.error) {
			await (() => new Promise(rs => setTimeout(rs, 1000)))();

			window.location.href = `${baseApiUrl}/download?id=${encodeURIComponent(
				data.data.result
			)}&filename=${encodeURIComponent(video.videoDetails.title)}`;
			toast.update(toasted, {
				type: 'success',
				render: 'Descargando video',
				isLoading: false,
				closeOnClick: true,
				closeButton: true,
				autoClose: true
			});
		} else {
			toast.update(toasted, {
				type: 'error',
				render: 'Ha ocurrido un error',
				isLoading: false,
				closeOnClick: true,
				closeButton: true,
				autoClose: true
			});
		}

		setDownloading(false);
	};

	return (
		<div className="App">
			<h1>YouTube Converter</h1>
			<div className="form">
				<Input
					placeholder="URL o ID de YouTube"
					size="lg"
					fullWidth
					ref={input}
					helperColor={notFound ? 'error' : 'default'}
				/>
				<Button color="error" auto disabled={loading} onClick={loadVideo} flat>
					{loading ? (
						<>
							<Loading size="xs" color="currentColor" />
							<span style={{ marginLeft: '10px' }}>Buscando...</span>
						</>
					) : (
						<>
							<FontAwesome icon="fa-regular fa-magnifying-glass" />
							<span style={{ marginLeft: '10px' }}>Buscar</span>
						</>
					)}
				</Button>
			</div>
			{video.videoDetails ? (
				<div className="video">
					<div className="data">
						<img
							src={`https://i.ytimg.com/vi/${video.videoDetails.videoId}/mqdefault.jpg`}
							alt={video.videoDetails.title}
						/>
						<h4>{video.videoDetails.title}</h4>
						<p>
							{video.videoDetails.author?.name.replace(' - Topic', '')} -{' '}
							{secondsToCounter(+video.videoDetails.lengthSeconds)}
						</p>
					</div>
					<div className="downloads">
						<h3>Descargas</h3>
						<ul className="downloadList">
							{video.formats
								.filter(f => ['audio/mp4', 'video/mp4'].includes(f.mimeType.split(';')[0]))
								.filter(f => (f.hasVideo ? f.mimeType.split(';')[1].trim().startsWith('codecs="avc1.') : true))
								.sort((a, b) =>
									b.hasVideo ? +b.qualityLabel.split('p')[0] - (+a.qualityLabel?.split('p')[0] || a.audioBitrate) : 0
								)
								.map(f => (
									<li className="downloadOption" key={f.mimeType + ' ' + f.qualityLabel}>
										<div className="section">
											<div className="container">
												{f.hasVideo ? 'MP4' : 'MP3'} {f.hasVideo ? f.qualityLabel : f.audioBitrate + 'k'}
												<br />(
												<code>{f.mimeType.split(';')[1].split('=')[1].replaceAll('"', '').trim().split(',')[0]}</code>)
											</div>
										</div>
										<div className="section">
											<Button
												light
												color="secondary"
												auto
												rounded
												className="downloadButton"
												onClick={() => downloadVideo(f.hasVideo ? [f.itag, 140] : [null, f.itag])}
												disabled={isDownloading}
											>
												{isDownloading ? (
													<Loading size="xs" color="currentColor" />
												) : (
													<FontAwesome icon="fa-regular fa-download" />
												)}
											</Button>
										</div>
									</li>
								))}
						</ul>
					</div>
				</div>
			) : notFound ? (
				<p className="error">No se encontró el video.</p>
			) : null}
		</div>
	);
}

function secondsToCounter(seconds) {
	const hours = Math.floor(seconds / 3600);
	const minutes = Math.floor((seconds - hours * 3600) / 60);
	const secondsLeft = seconds - hours * 3600 - minutes * 60;

	return `${hours ? hours.toString().padStart(2, '0') + ':' : ''}${minutes.toString().padStart(2, '0')}:${secondsLeft
		.toString()
		.padStart(2, '0')}`;
}

export default App;
